.contact-details-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: rem(20px);
    padding: rem(10px);

    .contact-img {
        width: rem(350px);
    }
    .edit-logo {
        background-color: $clr2;
        position: absolute;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        border-radius: rem(10px);
        padding: rem(5px);
        img {
            width: rem(40px);
        }
    }
    .contact-details {
        position: relative;
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: rem(10px);
    }
    .MuiPaper-root {
        text-align: center;
        width: max-content;
    }

    @include for-mobile-layout {
        .contact-details {
            gap: rem(5px);
        }
        .MuiPaper-root {
            width: 100%;
        }
        .edit-logo {
            img {
                width: rem(25px);
            }
        }
        .contact-img {
            width: rem(250px);
        }
    }
}
