.contact-edit {
    form {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content;
        margin: 20px auto;
        gap: 10px;
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);
        .contact-name,
        .contact-email,
        .contact-phone {
            width: 100%;
            align-items: center;
            display: flex;
            justify-content: space-between;
            input {
                padding: 5px;
                text-align: center;
            }
        }
        .delete-btn {
            position: absolute;
            bottom: 0;
            right: 0;
            background-color: $clr2;
            border-radius: rem(10px);
        }
        button {
            margin-block-start: 10px;
        }
    }

    img {
        width: rem(40px);
    }

    @include for-mobile-layout {
        form {
            .delete-btn {
                img {
                    width: rem(25px);
                }
            }
        }
    }
}
